@keyframes blim {
  0% {
    border: 11px solid #FFD200; }
  50% {
    border: 11px solid #1965E8; }
  100% {
    border: 11px solid #FFD200; } }

@keyframes blim-reverse {
  0% {
    border: 9px solid #1965E8; }
  50% {
    border: 9px solid #FFD200; }
  100% {
    border: 9px solid #1965E8; } }

@keyframes left-to-right {
  from {
    transform: translateX(-100%);
    opacity: 0; }
  to {
    transform: translateX(0%);
    opacity: 1; } }

@keyframes visibilisness {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }

@keyframes draw-fill-line {
  0% {
    opacity: 0;
    stroke-dashoffset: 900px;
    stroke-dasharray: 931px; }
  30% {
    opacity: 1;
    stroke-dashoffset: 0;
    stroke-dasharray: 931px; }
  50% {
    opacity: 1;
    stroke-dashoffset: 0px;
    stroke-dasharray: 931px; }
  80% {
    opacity: 1;
    stroke-dashoffset: 0px;
    stroke-dasharray: 931px; }
  100% {
    opacity: 0;
    stroke-dashoffset: 0px;
    stroke-dasharray: 931px; } }

@keyframes filling-area {
  0% {
    fill: transparent; }
  20% {
    fill: #1965E8; }
  40% {
    fill: #1965E8; }
  60% {
    fill: #1965E8; }
  80% {
    fill: transparent; }
  100% {
    fill: transparent; } }

@keyframes rotating {
  from {
    transform: rotate(0deg); }
  to {
    transform: rotate(360deg); } }

@keyframes rotating-reverse {
  from {
    transform: rotate(360deg); }
  to {
    transform: rotate(0deg); } }

